/* Global styles */
body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  background-color: #f7f7f7;
  color: #333;
}

/* Hero section styles */
.home-container {
  text-align: center;
  padding: 50px 20px;
  position: relative; /* Needed for positioning overlay */
}

.hero-section {
  position: relative; /* Make the section relative to contain the overlay */
  background-image: url('../Images/TiffinBack.jpg'); /* Add a hero image URL */
  background-size: cover;
  background-position: center;
  height: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.hero-section::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.6); /* Light overlay with 60% opacity */
  z-index: 1; /* Ensure overlay is above the background */
}

.hero-content {
  position: relative; /* Ensure content is above the overlay */
  z-index: 2; /* Content above overlay */
  max-width: 800px;
}

.hero-heading {
  font-size: 3.5rem;
  font-weight: 700;
  margin-bottom: 20px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  color: #333;
}

.hero-subheading {
  font-size: 1.5rem;
  margin-bottom: 40px;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.5);
  color: #333;
}

.order-now-button {
  padding: 15px 30px;
  font-size: 1.2rem;
  color: white;
  background-color: #e67e22;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.order-now-button:hover {
  background-color: #d35400;
}

/* Features section */
.features-section {
  padding: 40px 20px;
  background-color: #fff;
}

.features-heading {
  font-size: 2rem;
  margin-bottom: 40px;
  color: #e67e22;
}

.features-list {
  display: flex;
  justify-content: space-around;
  gap: 20px;
}

.feature-item {
  max-width: 300px;
}

.feature-item h3 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 10px;
}

.feature-item p {
  font-size: 1rem;
  color: #666;
}

/* CTA section */
.cta-section {
  padding: 40px 20px;
  background-color: #e67e22;
  color: white;
}

.cta-section h2 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.cta-button {
  padding: 15px 30px;
  font-size: 1.2rem;
  color: Black;
  background-color: #fff;
  border: none;
  border-radius: 30px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.cta-button:hover {
  background-color: #d35400;
  color: white;
}
